.btn {
  display: inline-flex;
  color: #ffffff;
  border: 2px solid #ffffff;
  background-color: inherit;
  padding: 27px 56px;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  letter-spacing: 0.04rem;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
@media (hover: hover) and (hover: hover) {
  .btn:hover:not(.focus-visible):hover:not(.focus-visible) {
    background-color: #ffffff;
    color: #103aae;
  }
}
@media (hover: hover) {
  .btn.focus-visible:focus:hover:not(.focus-visible), .btn:focus-visible:hover:not(.focus-visible) {
    background-color: #ffffff;
    color: #103aae;
  }
}
